  import * as actions from "./constant"


  
  const defaultstate=[]
  let id=0
  export default function homepagereducer(state=defaultstate,action){
      switch(action.type){
        default:
            return state;
            case actions.MSG_ADDED:
             return (action.payload)
              case actions.MSG_REMOVED:
               return(state.filter(e=>e!==action.payload.id))
      }
  }
  